import {GET_METHOD} from "src/services/Request";
import instance from "src/services/RequestInstances/NasaImageInstance";

export default class NasaImageClient {
    send(request, params = {}, method = GET_METHOD) {
        switch (method) {
            case GET_METHOD:
                return this.get(request, params);
            default:
                return new Promise(() => {
                });
        }
    }

    /**
     *
     * @param request
     * @param params
     * @returns {*}
     */
    get(request, params = {}) {
        return instance.get(request, params);
    }
}

