import React, {Component} from 'react';
import {Link} from 'react-router-dom';
import AppIcon from "src/components/App/Icon/AppIcon";
import AppInput from "src/components/App/Input/AppInput";
import AppTitle from "src/components/App/Title/AppTitle";
import CollectionList from "src/components/Collection/List/CollectionList";
import NasaImageSearchRequest from "src/services/Request/NasaImageSearchRequest";
import RequestFactory from "src/services/RequestFactory";
import {ReactComponent as BackIcon} from 'src/assets/icons/back.svg';
import classes from "./NasaCollectionSearch.module.scss";
import {CancelToken} from 'axios';

class NasaCollectionSearch extends Component {
    searchRequestCancelToken = null;
    state = {
        items: [],
    };

    render() {
        return (
            <>
                <div>
                    <Link to="/" className={classes.backLink}>
                        <AppIcon icon={BackIcon} small={1}/>
                        <span>
                            Back to Collection
                        </span>
                    </Link>
                </div>
                <AppTitle>Search from Nasa</AppTitle>
                <AppInput placeholder={'Type something to search...'}
                          onChange={this.searchImages.bind(this)}/>
                <CollectionList items={this.state.items} hideAddedItems={true}/>
            </>
        );
    }

    searchImages(e) {
        if (this.searchRequestCancelToken !== null) {
            this.searchRequestCancelToken();
        }
        if (e.target.value.length > 1) {
            const factory = new RequestFactory();
            factory.send(new NasaImageSearchRequest(), {
                params: {
                    q: e.target.value
                },
                cancelToken: new CancelToken((c) => {
                    this.searchRequestCancelToken = c;
                }),
            })
                .then((res) => {
                    return res.data.collection.items;
                })
                .then((items) => {
                    this.setState({
                        items: items,
                    });
                }).catch(e => {
            });
        }
    }
}

export default NasaCollectionSearch;
